var React = require('react');
var {CreateForm, SubmitButton} = require('../../../src/form');

var Form = CreateForm({

  getSchema: function () {
    return {
      name: {
        label: 'Name',
        type: 'string'
      },
      email: {
        label: 'Email',
        type: 'string'
      }
    }
  },

  onSuccess: function (data) {
    alert(JSON.stringify(data));
  },

  render: function () {
    return (React.createElement("form", null, 

      React.createElement("p", null, 
        React.createElement("label", null, "Name"), 
        React.createElement("input", {type: "text", valueLink: this.linkField('name')})
      ), 

      React.createElement("p", null, 
        React.createElement("label", null, "Email"), 
        React.createElement("input", {type: "text", valueLink: this.linkField('email')})
      ), 

      React.createElement("p", null, React.createElement(SubmitButton, null)), 
      React.createElement("p", null, React.createElement("button", {onClick: this.submitForm}, "This will also submit"))

    ));
  }
});

module.exports = Form;




