var React = require('react/addons');

var contextTypes = {
  activePage: React.PropTypes.number,
  navigateTo: React.PropTypes.func,
  goNext: React.PropTypes.func
};

var NavButton = React.createClass({displayName: "NavButton",
  render: function () {
    return (React.createElement("a", {className: 'sbs-nav-btn' + (this.props.active ? ' active' : ''), onClick: this.props.onClick}, 
      React.createElement("span", {className: "sbs-nav-btn-index"}, this.props.index + 1), 
      React.createElement("span", {className: "sbs-nav-btn-title"}, this.props.title)
    ));
  }
});

var StepMixin = {
  contextTypes,
  activePage: function () {
    return this.context.activePage;
  },
  navigateTo: function (i) {
    this.context.navigateTo(i);
  },
  goNext: function () {
    this.context.goNext();
  }
};

var StepByStep = React.createClass({displayName: "StepByStep",
  childContextTypes: contextTypes,
  getChildContext: function() {
    return {
      activePage: this.state.activePage,
      navigateTo: this.navigateTo,
      goNext: this.goNext
    };
  },
  getInitialState: function () {
    return {
      activePage: 0
    };
  },
  navigateTo: function (i) {
    this.setState({activePage: i});
  },
  goNext: function () {
    this.setState({activePage: this.state.activePage + 1});
  },
  render: function () {
    return (React.createElement("div", {className: "step-by-step"}, 
      React.createElement("div", {className: "sbs-nav"}, 
        this.props.children.map((page, i) => React.createElement(NavButton, {active: i === this.state.activePage, index: i, title: page.props.title, onClick: this.props.interactiveNav ? () => this.navigateTo(i) : null}))
      ), 
      this.props.children.map((page, i) => {
        return (React.createElement("div", {hidden: i !== this.state.activePage}, 
          React.addons.cloneWithProps(page)
        ));
      })
    ));
  }
});

var NextButton = React.createClass({displayName: "NextButton",
  mixins: [StepMixin],
  onClick: function (e) {
    e.preventDefault();
    if (this.props.disabled) return;
    this.goNext();
  },
  render: function () {
    return (React.createElement("button", {onClick: this.onClick, disabled: this.props.disabled}, 
      this.props.children || 'Next'
    ));
  }
});

module.exports = {
  StepByStep,
  StepMixin,
  NextButton
};



