var React = require('react/addons');
var Formation = require('../../../src/form');

var CreateForm = Formation.CreateForm;
var SubmitButton = Formation.SubmitButton;
var ErrorMessage = Formation.ErrorMessage;
var Validator = Formation.Validator;

// We need FormMixin so this child component
// can have access to the global form API
var FormMixin = Formation.FormMixin;

var CreditCard = React.createClass({displayName: "CreditCard",

  mixins: [FormMixin],

  render: function () {
    return (React.createElement("div", {className: "form-group"}, 
      React.createElement("label", null, "Enter your credit card number (try 4111111111111111):"), 
      React.createElement("input", {type: "number", valueLink: this.linkField('cardNumber')}), 
      React.createElement(ErrorMessage, {field: "cardNumber"})
    ));
  }
});

var PersonalInfo = React.createClass({displayName: "PersonalInfo",

  mixins: [FormMixin],

  render: function () {
    return (React.createElement("div", null, 
      React.createElement("div", {className: "form-group"}, 
        React.createElement("label", null, "Username"), 
        React.createElement("input", {type: "text", name: "username", valueLink: this.linkField('username')}), 
        React.createElement(ErrorMessage, {field: "username"})
      ), 
      React.createElement("div", {className: "form-group"}, 
        React.createElement("label", null, "Email"), 
        React.createElement("input", {type: "text", name: "email", valueLink: this.linkField('email')}), 
        React.createElement(ErrorMessage, {field: "email"})
      )
    ));
  }
});

var Form = CreateForm({
  getSchema: function () {
    return {
      username: {
        required: true,
        label: 'Username',
        type: Validator.alpha()
      },
      email: {
        required: true,
        label: 'Email',
        type: Validator.email()
      },
      cardNumber: {
        type: Validator.creditCard(),
        label: 'Credit card'
      }
    };
  },
  onSuccess: function (data) {
    alert(JSON.stringify(data));
  },
  render: function () {
    return (React.createElement("form", null, 
      React.createElement(PersonalInfo, null), 
      React.createElement(CreditCard, null), 
      React.createElement("p", null, React.createElement(SubmitButton, null))
    ));
  }
});

module.exports = Form;



