var React = require('react');
var Formation = require('../../../src/form');

var CreateForm = Formation.CreateForm;
var ErrorMessage = Formation.ErrorMessage;

var PersonalInfo = CreateForm({

  getSchema: function () {
    return {
      name: {
        label: 'Name',
        type: 'string',
        required: true
      },
      email: {
        label: 'Email',
        type: 'email',
        required: true
      }
    }
  },

  onSuccess: function (data) {
    alert(JSON.stringify(data));
  },

  render: function () {

    var inputs = [
      React.createElement("div", {className: "form-group"}, 
        React.createElement("label", null, "Name"), 
        React.createElement("input", {type: "text", name: "name", valueLink: this.linkField('name')}), 
        React.createElement(ErrorMessage, {field: "name"})
      ),

      React.createElement("div", {className: "form-group"}, 
        React.createElement("label", null, "Email"), 
        React.createElement("input", {type: "text", name: "email", valueLink: this.linkField('email')}), 
        React.createElement(ErrorMessage, {field: "email"})
      )
    ];

    if (this.props.emailFirst) inputs.reverse();

    var style = {
      padding: 20,
      backgroundColor: this.props.backgroundColor
    };

    return (React.createElement("form", {style: style}, 
      inputs, 
      React.createElement("p", null, React.createElement("button", {onClick: this.submitForm}, "Submit"))
    ));
  }
});


var Form = React.createClass({displayName: "Form",
  render: function () {
    return (React.createElement("div", null, 
      React.createElement("h3", null, "White background"), 
      React.createElement(PersonalInfo, null), 

      React.createElement("h3", null, "Yellow background, email first"), 
      React.createElement(PersonalInfo, {emailFirst: true, backgroundColor: "#FFEEBC"})
    ));
  }
});

module.exports = Form;




