var React = require('react');
var CreateForm = require('../../../src/form').CreateForm;

var Form = CreateForm({

  // This defines all the fields in the form
  getSchema: function () {
    return {
      name: {
        label: 'Name',
        type: 'string'
      },
      email: {
        label: 'Email',
        type: 'string'
      }
    };
  },

  // This code is run when the form is valid and submitted
  onSuccess: function (data) {
    alert(JSON.stringify(data));
  },

  render: function () {
    return (React.createElement("form", null, 

      React.createElement("div", {className: "form-group"}, 
        React.createElement("label", null, "Name"), 
        React.createElement("input", {type: "text", valueLink: this.linkField('name')})
      ), 

      React.createElement("div", {className: "form-group"}, 
        React.createElement("label", null, "Email"), 
        React.createElement("input", {type: "text", valueLink: this.linkField('email')})
      ), 

      React.createElement("div", {className: "form-group"}, 
        React.createElement("button", {onClick: this.submitForm}, "Submit")
      )

    ));
  }
});

module.exports = Form;




