var React = require('react');
var Formation = require('../../../src/form');

var CreateForm = Formation.CreateForm;
var ErrorMessage = Formation.ErrorMessage;
var Radio = Formation.Radio;

var Form = CreateForm({

  getSchema: function () {
    return {
      name: {
        label: 'Name',
        type: 'string',
        required: true
      },
      color: {
        label: 'Favourite Color',
        type: 'string',
        required: true
      }
    };
  },

  onSuccess: function (data) {
    alert(JSON.stringify(data));
  },

  render: function () {
    var colorLink = this.linkField('color');
    return (React.createElement("form", null, 

      React.createElement("div", {className: "form-group"}, 
        React.createElement("label", null, "Name"), 
        React.createElement("input", {type: "text", name: "name", valueLink: this.linkField('name')}), 
        React.createElement(ErrorMessage, {field: "name"})
      ), 

      React.createElement("div", {className: "form-group"}, 
        React.createElement("label", null, "What is your favourite colour?"), 
        React.createElement("p", null, React.createElement(Radio, {name: "color", value: "blue", radioLink: colorLink}), " Blue"), 
        React.createElement("p", null, React.createElement(Radio, {name: "color", value: "red", radioLink: colorLink}), " Red"), 
        React.createElement("p", null, React.createElement(Radio, {name: "color", value: "green", radioLink: colorLink}), " Green")
      ), 

      React.createElement("p", null, React.createElement("button", {onClick: this.submitForm}, "Submit"))

    ));
  }
});

module.exports = Form;




