var React = require('react/addons');
var Formation = require('../../../src/form');

var CreateForm = Formation.CreateForm;
var SubmitButton = Formation.SubmitButton;
var ErrorMessage = Formation.ErrorMessage;

var zxcvbn = require('zxcvbn');
var classnames = require('classnames');

var Form = CreateForm({
  getSchema: function () {
    return {
      username: {
        required: true,
        label: 'Username',
        type: function (val) {
          if (/^[a-zA-Z0-9\-]{1,20}$/.test(val)) return false;
          return 'Must be 1-20 characters long and use only "-" and alphanumeric symbols';
        }
      },
      email: {
        required: true,
        label: 'Email',
        type: 'email'
      },
      password: {
        required: true,
        label: 'Password',
        type: function (val) {
          if (val && zxcvbn(val).score > 0) return false;
          return 'Password is not strong enough';
        },
      },
      subscribe: {
        type: 'boolean'
      }
    }
  },

  getInitialState: function () {
    return {
      blurred: {}
    };
  },
  onSuccess: function (data) {
    alert(JSON.stringify(data));
  },
  onBlur: function (field) {
    return () => {
      var blurred = this.state.blurred;
      blurred[field] = true;
      this.setState({blurred});
    }
  },
  getPassStrength: function () {
    return this.state.password ? zxcvbn(this.state.password).score : -1;
  },
  passStrengthStrings: ['Not strong enough', 'Weak', 'OK', 'Good', 'Strong'],

  render: function () {
    var passStrength = this.getPassStrength();
    return (React.createElement("form", {className: "login-eg animated fadeInUp"}, 

      React.createElement("div", {className: "body"}, 
        React.createElement("div", {className: "form-group"}, 
          React.createElement("label", {hidden: !this.props.showLabels}, "Username"), 
          React.createElement("input", {placeholder: this.props.showLabels ? '' : 'Username', type: "text", onBlur: this.onBlur('username'), valueLink: this.linkField('username')}), 
          React.createElement(ErrorMessage, {show: this.state.blurred.username, field: "username"})
        ), 

        React.createElement("div", {className: "form-group"}, 
          React.createElement("label", {hidden: !this.props.showLabels}, "Email"), 
          React.createElement("input", {placeholder: this.props.showLabels ? '' : 'Email', type: "text", onBlur: this.onBlur('email'), valueLink: this.linkField('email')}), 
          React.createElement(ErrorMessage, {show: this.state.blurred.email, field: "email"})
        ), 

        React.createElement("div", {className: "form-group"}, 
          React.createElement("label", {hidden: !this.props.showLabels}, "Password"), 
          React.createElement("input", {placeholder: this.props.showLabels ? '' : 'Password', type: "password", name: "password", onBlur: this.onBlur('password'), valueLink: this.linkField('password')}), 
          React.createElement("div", {className: 'password-strength strength-' + passStrength}, 
            passStrength >= 0 && this.passStrengthStrings[passStrength], 
            React.createElement("span", {className: "indicator", style: {width: ((passStrength + 1) * 20) + '%'}})
          ), 
          React.createElement(ErrorMessage, {show: this.state.blurred.password, field: "password"})
        )
      ), 

      React.createElement("div", {className: "submit-footer"}, 
        React.createElement(SubmitButton, {style: {backgroundColor: this.props.buttonColor}}, this.props.signUpMessage || 'Sign Up')
      )
    ));
  }
});

module.exports = Form;



