var React = require('react');
var Formation = require('../../../src/form');

var CreateForm = Formation.CreateForm;
var ErrorMessage = Formation.ErrorMessage;

var Form = CreateForm({

  getSchema: function () {
    return {
      name: {
        label: 'Name',
        type: 'string',
        required: true
      },
      email: {
        label: 'Email',
        type: 'email',
        required: true
      }
    };
  },

  onSuccess: function (data) {
    alert(JSON.stringify(data));
  },

  render: function () {
    return (React.createElement("form", null, 

      React.createElement("div", {className: "form-group"}, 
        React.createElement("label", null, "Name (error is shown after submit attempt)"), 
        React.createElement("input", {type: "text", name: "name", valueLink: this.linkField('name')}), 
        React.createElement(ErrorMessage, {field: "name"})
      ), 

      React.createElement("div", {className: "form-group"}, 
        React.createElement("label", null, "Email (error is shown immediately)"), 
        React.createElement("input", {type: "text", name: "email", valueLink: this.linkField('email')}), 
        React.createElement(ErrorMessage, {show: true, field: "email"})
      ), 

      React.createElement("p", null, React.createElement("button", {onClick: this.submitForm}, "Submit"))

    ));
  }
});

module.exports = Form;




