var React = require('react/addons');
var Router = require('react-router');
var {DefaultRoute, Link, Route, RouteHandler} = Router;
var {Markdown} = require('react-markdocs');
var IntlMixin = require('react-intl').IntlMixin;

var routeDefinitions = [
  React.createElement(DefaultRoute, {name: "simple", label: "Get started", handler: require('./pages/simple/docs.jsx')}),
  React.createElement(Route, {name: "linking", label: "Adding inputs", handler: require('./pages/linking/docs.jsx')}),
  React.createElement(Route, {name: "children", label: "Building modular UI", handler: require('./pages/children/docs.jsx')}),
  React.createElement(Route, {name: "validations", label: "Validation", handler: require('./pages/validations/docs.jsx')}),
  React.createElement(Route, {name: "errors", label: "Errors", handler: require('./pages/errors/docs.jsx')}),
  React.createElement(Route, {name: "submitting", label: "Submitting", handler: require('./pages/submitting/docs.jsx')}),
  React.createElement(Route, {name: "schema", label: "Schema", handler: require('./pages/schema/docs.jsx')}),
  React.createElement(Route, {name: "multi", label: "Multi-part forms", handler: require('./pages/multi/docs.jsx')}),
  React.createElement(Route, {name: "ab", label: "A/B testing", handler: require('./pages/ab/docs.jsx')}),
  React.createElement(Route, {name: "inputTypes", label: "Input types reference", handler: require('./pages/inputTypes/docs.jsx')}),
  React.createElement(Route, {name: "airbnb", label: "Example: airbnb", handler: require('./pages/airbnb/docs.jsx')})
];

var intlData = {
  locales : ['en-US'],
  messages: {
    birthday: 'Birthday',
    foo: 'foo'
  }
};

var App = React.createClass({displayName: "App",
  mixins: [IntlMixin],
  render: function () {
    return (React.createElement(RouteHandler, null));
  }
});

var Examples = React.createClass({displayName: "Examples",
  render: function () {
    return (
      React.createElement("div", {className: "container"}, 
        React.createElement("header", {className: "links"}, 
          React.createElement("h2", null, "Guide"), 
          React.createElement("ul", null, 
            React.createElement("li", null, React.createElement(Link, {to: "/"}, "Home")), 
            routeDefinitions.map(route => React.createElement("li", null, React.createElement(Link, {to: route.props.name}, route.props.label)))
          )
        ), 
        React.createElement("div", {className: "body"}, 
          React.createElement(RouteHandler, null)
        )
      )
    );
  }
});

var ExamplesHome = React.createClass({displayName: "ExamplesHome",
  render: function () {
    return (React.createElement("div", {className: "docs"}, 
      React.createElement(Markdown, {source: require('../README.md').split('## Guide and examples')[0]})
    ));
  }
});

var routes = (
  React.createElement(Route, {name: "app", path: "/", handler: App}, 
    React.createElement(Route, {name: "examples", handler: Examples}, 
      routeDefinitions
    ), 
    React.createElement(DefaultRoute, {handler: require('./home.jsx')})
  )
);

Router.run(routes, function (Handler) {
  React.render(React.createElement(Handler, React.__spread({},  intlData)), document.getElementById('app'));
});



