var React = require('react/addons');
var Login = require('./pages/login/index.jsx');
var {Link} = require('react-router');

function generateVariant(propsets) {
  var props = Object.keys(propsets);
  var variation = {};
  props.forEach(prop => {
    var variants = propsets[prop];
    variation[prop] = variants[Math.floor(Math.random() * (variants.length))];
  });
  return variation;
}

var variants = {
  showLabels: [true, false],
  signUpMessage: ['Sign up', 'Join', 'Join us', 'One. Of. Us.'],
  buttonColor: ['#00BE94', '#69A0FC']
};

var GithubLink = React.createClass({displayName: "GithubLink",
  render: function () {
    return (React.createElement("iframe", {
      className: "gh-star", src: "https://ghbtns.com/github-btn.html?user=k88hudson&repo=react-formation&type=star&count=true&size=large", 
      frameborder: "0", 
      scrolling: "0", 
      width: "160px", 
      height: "30px"}));
  }
});

module.exports = React.createClass({displayName: "module.exports",

  getInitialState: function () {
    return {
      variants: generateVariant(variants)
    };
  },

  refreshTest: function (e) {
    e.preventDefault();
    this.setState({variants: generateVariant(variants)});
  },

  render: function () {

    return (React.createElement("div", {className: "home"}, 
      React.createElement("header", null, 
        React.createElement("h1", null, "Build robust, testable forms in minutes", React.createElement("br", null), "with ", React.createElement("strong", null, "React Formation")), 
        React.createElement("p", null, React.createElement(Link, {to: "examples"}, "See the guide"))
      ), 

      React.createElement(Login, React.__spread({},  this.state.variants)), 
      React.createElement("footer", null, 
        React.createElement("p", null, "This login form was built in ", React.createElement("a", {href: "https://github.com/k88hudson/react-formation/blob/gh-pages/examples/pages/login/index.jsx"}, "under 100 lines of code"), " and includes:"), 
        React.createElement("ul", null, 
          React.createElement("li", null, "Advanced custom validations for each field"), 
          React.createElement("li", null, "Password strength testing"), 
          React.createElement("li", null, "Pre-submit validation"), 
          React.createElement("li", null, "Smart display of error messages"), 
          React.createElement("li", null, "Hooks for A/B testing variations (", React.createElement("a", {href: "#", onClick: this.refreshTest}, "try refreshing!"), ")")
        ), 
        React.createElement("p", null, React.createElement(GithubLink, null))
      )
    ));
  }
});



