var React = require('react/addons');
var {Markdown} = require('react-markdocs');
// var hljs = require('highlight.js');
var PrismCode = require('./lib/Prism.jsx');

module.exports = function(options) {
  return React.createClass({
    renderExample: function () {
      var Example = options.example;
      var code = options.code
        .replace('../../src/form', 'react-formation')
        .replace('module.exports = Form;', 'React.render(<Form />, document.body);');

      return (React.createElement("div", null, 
        React.createElement("h2", null, "Complete Example"), 
        React.createElement("div", {className: "example"}, React.createElement(Example, null)), 
        React.createElement(PrismCode, {className: "language-jsx"}, 
          code
        )
      ));
    },
    render: function () {
      return (React.createElement("div", {className: "docs"}, 
        React.createElement("div", {className: "markdown"}, React.createElement(Markdown, {prism: true, source: options.docs, options: {html: true}})), 
        options.example && options.code && this.renderExample()
      ));
    }
  });
}



