var React = require('react/addons');
var Formation = require('../../../src/form');
var classnames = require('classnames');

var CreateForm = Formation.CreateForm;
var SubmitButton = Formation.SubmitButton;
var ErrorMessage = Formation.ErrorMessage;
var Validator = Formation.Validator;

var {FormattedMessage, IntlMixin} = require('react-intl').IntlMixin;

var days = [];
var years = [];
for (var i = 2015; i > 1900; i--) {years.push(i);}
for (var j = 1; j < 32; j++) {days.push(j);}
var dateData = {
  years,
  months: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
  days
};


var Input = React.createClass({displayName: "Input",
  mixins: [Formation.FormMixin],
  render: function () {
    var errors = this.validateField(this.props.field);
    var inputClass = classnames('input', {
      valid: this.didSubmit() && !errors,
      invalid: this.didSubmit() && errors
    });
    return (React.createElement("div", {className: "form-group"}, 
      React.createElement(ErrorMessage, {className: "helper-error", field: this.props.field}), 
      React.createElement("input", {className: inputClass, type: this.props.type || 'text', placeholder: this.props.label, valueLink: this.linkField(this.props.field)})
    ));
  }
});

var Form = CreateForm({
  mixins: [IntlMixin],
  getSchema: function () {
    return {
      firstName: {
        required: true,
        type: Validator.maxLength(5),
        label: 'First name'
      },
      lastName: {
        required: true,
        label: 'Last name',
        type: 'string'
      },
      email: {
        label: 'Email',
        required: true,
        type: 'email'
      },
      password: {
        required: true,
        label: 'Password',
        type: function (val) {
          if (val.length >= 5) return false;
          return 'Password must be at least 5 characters. Please try again';
        },
      },
      birthdayMonth: {
        required: true
      },
      birthdayDay: {
        required: true
      },
      birthdayYear: {
        required: true
      },
      mailingList: {
        type: 'boolean'
      }
    };
  },
  onSuccess: function (data) {
    alert(JSON.stringify(data));
  },
  render: function () {
    return (React.createElement("div", {className: "airbnb-eg"}, 
      React.createElement("form", null, 
        React.createElement(Input, {label: "First name", field: "firstName"}), 
        React.createElement(Input, {label: "Last name", field: "lastName"}), 
        React.createElement(Input, {label: "Email", field: "email"}), 
        React.createElement(Input, {label: "Password", field: "password", type: "password"}), 

        React.createElement("label", null, React.createElement(FormattedMessage, {message: this.getIntlMessage('birthday')})), 
        React.createElement("div", {className: "helper-error", hidden: !this.didSubmit() || (!this.validateField('birthdayMonth') && !this.validateField('birthdayDay') && !this.validateField('birthdayYear'))}, 
          "Select your birth date to continue"
        ), 
        React.createElement("select", {valueLink: this.linkField('birthdayMonth')}, 
          React.createElement("option", {value: ""}, "Month"), 
          dateData.months.map(month => React.createElement("option", {value: month}, month))
        ), 
        React.createElement("select", {valueLink: this.linkField('birthdayDay')}, 
          React.createElement("option", {value: ""}, "Day"), 
          dateData.days.map(day => React.createElement("option", {value: day}, day))
        ), 
        React.createElement("select", {valueLink: this.linkField('birthdayYear')}, 
          React.createElement("option", {value: ""}, "Year"), 
          dateData.years.map(year => React.createElement("option", {value: year}, year))
        ), 
        React.createElement("label", {className: "checkbox"}, 
          React.createElement("input", {type: "checkbox", checkedLink: this.linkField('mailingList')}), 
          "I'd like to receive coupons and inspiration"
        ), 
        React.createElement("p", {className: "terms"}, "By signing up, I agree to Airbnb's Terms of Service, Privacy Policy, Guest Refund Policy, and Host Guarantee Terms."), 
        React.createElement("div", {className: "submit-footer"}, 
          React.createElement(SubmitButton, null, "Sign up")
        )
      )
    ));
  }
});

module.exports = Form;



